import {
  Grid,
  Typography,
  Container,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Snackbar,
} from "@material-ui/core";
import Button from "@material-ui/core/Button";
import styled from "styled-components";
import { useState, useEffect } from "react";
import { useWeb3React } from "@web3-react/core";
import {
  useControllerContract,
  useHoldContract,
  useRouterContract,
} from "../../hooks/index";
import { formatEther, parseEther } from "@ethersproject/units";
import config from "../../utils/config.json";
import { Alert } from "@material-ui/lab";

function Dashboard() {
  const { account, library } = useWeb3React();
  const [snackBar, setSnackBar] = useState({
    status: false,
    type: "",
    message: "",
  });
  const [holdBalance, setHoldBalance] = useState(0.0);
  const [totalSupply, setTotalSupply] = useState(0.0);
  const [timeLock, setTimeLock] = useState(0);

  const [wbnbBalance, setWbnbBalance] = useState(0.0);
  const [busdBalance, setBusdBalance] = useState(0.0);

  const controllerContract = useControllerContract();
  const routerContract = useRouterContract();
  const holdContract = useHoldContract();

  useEffect(() => {
    let stale = false;
    const initData = async () => {
      const bal = await holdContract.balanceOf(account);
      const dBal = formatEther(bal);

      if (!stale) {
        setHoldBalance(dBal);
      }

      try {
        const timeLockVal = await controllerContract.timeLocksecs();
        if (!stale) {
          setTimeLock(parseInt(timeLockVal));
        }
      } catch (e) {
        console.log(e);
      }

      const totalTokenSupply = await holdContract.totalSupply();
      const totalTokenSupplyBal = formatEther(totalTokenSupply);
      if (!stale) {
        setTotalSupply(totalTokenSupplyBal);
      }

      try {
        const amountsOut = await routerContract.getAmountsOut(
          parseEther("1.0"),
          [
            config.HOLD_CONTRACT_ADDRESS,
            config.WBNB_ADDRESS,
            config.BUSD_ADDRESS,
          ]
        );

        const wbnbBal = formatEther(amountsOut[1]);
        if (!stale) {
          setWbnbBalance(wbnbBal);
        }

        const busdBal = formatEther(amountsOut[2]);
        if (!stale) {
          setBusdBalance(busdBal);
        }
      } catch (e) {
        console.log(e);
      }
    };

    initData();

    return () => {
      stale = true;
    };
  }, [account, controllerContract, holdContract, routerContract]);

  const startHarvest = async () => {
    try {
      const harvested = await controllerContract.harvest();

      library.once(harvested.hash, (done) => {
        if (done.status === 1) {
          setSnackBar({
            status: true,
            type: "success",
            message: `Harvest success.`,
          });
        } else {
          setSnackBar({
            status: true,
            type: "error",
            message: `Harvest failed.`,
          });
        }
      });
    } catch (e) {
      console.log(e);
    }
  };

  const handleSnackBarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setSnackBar({
      status: false,
      type: "",
      message: "",
    });
  };
  return (
    <Container maxWidth="sm">
      <Grid>
        <StyledWrapper>
          <div class="top-bar">
            <Typography variant="h6" align="center" color="textSecondary">
              YOU HAVE
            </Typography>
            <Typography variant="h3" align="center">
              {parseFloat(holdBalance).toFixed(2)} <b>HOLD</b>
            </Typography>
          </div>

          <div className="farm-box">
            <Button
              className="styled-button"
              fullWidth
              variant="link"
              color="primary"
              href="https://exchange.pancakeswap.finance/#/swap?outputCurrency=0x593bdbaf7e65c51f5a35761cc688245a560e6a02"
              target="_blank"
            >
              Get HOLD from Pancakeswap
            </Button>

            <Button
              className="styled-button"
              fullWidth
              variant="contained"
              color="primary"
              onClick={() => startHarvest()}
              disabled={timeLock < 86400}
            >
              Harvest
            </Button>
            <Alert severity="info" color="info">
              You can harvest one time a day.
            </Alert>
          </div>

          <List>
            <ListItem>
              <ListItemText primary="Total Supply" />
              <ListItemSecondaryAction>
                {parseFloat(totalSupply).toFixed(4)} HOLD
              </ListItemSecondaryAction>
            </ListItem>
          </List>
          <List>
            <ListItem>
              <ListItemText primary="1 HOLD" />
              <ListItemSecondaryAction>
                <div className="sub-item">
                  {parseFloat(wbnbBalance).toFixed(4)} WBNB
                </div>
                <div className="sub-item">
                  {parseFloat(busdBalance).toFixed(4)} BUSD
                </div>
              </ListItemSecondaryAction>
            </ListItem>
          </List>
          <List>
            <ListItem>
              <ListItemText primary="TVL" />
              <ListItemSecondaryAction>
                <div className="sub-item">
                  {parseFloat(wbnbBalance * totalSupply).toFixed(4)} WBNB
                </div>
                <div className="sub-item">
                  {parseFloat(busdBalance * totalSupply).toFixed(4)} BUSD
                </div>
              </ListItemSecondaryAction>
            </ListItem>
          </List>
        </StyledWrapper>
      </Grid>

      <Snackbar
        open={snackBar.status}
        autoHideDuration={6000}
        onClose={handleSnackBarClose}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert
          variant="filled"
          onClose={handleSnackBarClose}
          severity={snackBar.type}
        >
          {snackBar.message}
        </Alert>
      </Snackbar>
    </Container>
  );
}

const StyledWrapper = styled.div`
  background: rgba(255, 255, 255, 0.86);
  padding: 24px;
  margin: 24px 0;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 16px;
  .input-field {
    margin-bottom: 24px;
  }
  .top-bar {
    position: relative;
    overflow: hidden;
    .info-button {
      position: absolute;
      top: 0;
      right: 0;
    }
  }

  .styled-button {
    height: 75px;
    margin: 8px 0;
  }

  .farm-box {
    margin: 16px 0;
    padding: 12px;
  }

  .sub-item {
    clear: both;
    text-align: right;
    padding-bottom: 4px;
  }
`;

export default Dashboard;
