import { Button, Grid, Typography } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import { useWeb3React } from "@web3-react/core";
import { getErrorMessage } from "./../../hooks/index";
import styled from "styled-components";

const ConnectWallet = (props) => {
  const { error } = useWeb3React();

  const { connectorsByName, activate, setActivatingConnector } = props;

  const activateWallet = async (currentConnector, name) => {
    setActivatingConnector(currentConnector);
    await activate(connectorsByName[name]);
    window.localStorage.setItem("wallet", name);
  };

  return (
    <StyledConnect>
      <Grid container direction="column" justify="center" alignItems="center">
        <Typography variant="h4" gutterBottom align="center">
          Farming powered by Auto Farm Network.
        </Typography>
        <div className="connectors">
          {connectorsByName &&
            Object.keys(connectorsByName).map((name) => {
              const currentConnector = connectorsByName[name];
              return (
                <div key={name}>
                  {currentConnector && name === "Injected" && (
                    <Button
                      variant="outlined"
                      color="primary"
                      key={name}
                      onClick={() => {
                        activateWallet(currentConnector, name);
                      }}
                    >
                      <p className="button-label">Connect Your Wallet</p>
                    </Button>
                  )}
                </div>
              );
            })}
        </div>
        <div className="error-message">
          {!!error && (
            <Alert severity="error">
              <div
                dangerouslySetInnerHTML={{ __html: getErrorMessage(error) }}
              />
            </Alert>
          )}
        </div>
      </Grid>
    </StyledConnect>
  );
};

const StyledConnect = styled.div`
  margin: 80px 0;

  .connectors {
    clear: both;
    position: flex;
    flex-direction: row;
    margin: 24px 0;
  }

  .button-label {
    font-weight: 700;
    margin-left: 8px;
  }
`;

export default ConnectWallet;
